.messagesContainer {
    height: 400px;
    overflow-y: auto;
    overflow-x: hidden;
    border: 2px solid rgba(79, 79, 79, 0.8);
    border-radius: 10px;
}

.textareaBlock {
    margin-top: 10px;
}

.textareaBlock textarea {
    width: 100%;
    border-radius: 10px;
    outline: none;
    resize: none;
}

.formButton {
    border: 0;
    background-color: rgba(79,79,79, 0.8);
    border-radius: 10px;
    color: white;
    width: 100%;
    height: 35px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
}

.messagesContainer::-webkit-scrollbar {
    width: 8px;

}

.messagesContainer::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-border-radius: 0 10px 10px 0;


}

.messagesContainer::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0 10px 10px 0;
    background: rgba(79, 79, 79, 0.8);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

.messagesContainer::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(78, 72, 72, 0.4);
}


